.homepage__main__wrapper {
    background-image: url(/images/salon.jpg);
    background-size: cover;
    width: 100%;
    height: 100vh;
    padding-top: 5rem;
    &::after {
        content: "";
        background: black;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 1;
        opacity: .7;
    }
    .home__inner {
        position: relative;
        width: 65%;
        margin: 0 auto;
        padding: 1.85rem;
        z-index: 3;
        background: white;
        // border: lightpink 5px solid;
        &::before {
            content: '';
            width: calc(100% + 1rem);
            height: calc(100% + 1rem);
            position: absolute;
            // background: linear-gradient(50% , rgb(253,183,219), rgb(195,229,242));
            background-color: #9ab5e1;
            background-image: linear-gradient(315deg, #9ab5e1 0%, #f39fdc 74%);
            top: calc(0% - 8px);
            left: calc(0% - 8px);
            z-index: -2;
        }
        &::after {
            content: '';
            width: 100%;
            height: 100%;
            position: absolute;
            background: white;
            top: 0;
            left: 0;
            z-index: 1;
        }
        .main__title {
            font-weight: 700;
            text-align: center;
            position: relative;
            z-index: 4;
        }
        p {
            text-align: center;
            margin: .45rem 0;
            position: relative;
            z-index: 4;
        }
        .home__input__wrapper {
            width: 75%;
            margin: 1rem auto;
            position: relative;
            z-index: 4;
            .home__input {
                width: 100%;
                padding:  .85rem .35em .55rem .35em;
                font-size: 1rem;
                margin: .45rem 0;
                border-radius: 5px;
                background: white;
                border: 1px solid rgba(255,255,255,0.2);
                box-shadow:  
                -6px -6px 26px 0 rgba(255, 255, 255, 1 ),
                6px 6px 16px 0 rgba(217, 210, 200, 0.51)
            }
        }
        .home__button__wrapper {
            width: 75%;
            margin: 0 auto;
            text-align: center;
            position: relative;
            z-index: 4;
            input {
                width: 100%;
                padding:  .85rem .35em ;
                font-size: .85rem;
                margin: .45rem 0;
                text-transform: uppercase;
                border-radius: 5px;
                font-weight: 600;
                cursor: pointer;
                background: linear-gradient(315deg, #9ab5e1 0%, #f39fdc 74%);
                color: whitesmoke;
                border: 1px solid rgba(255,255,255,0.2);
                box-shadow:  
                -6px -6px 26px 0 rgba(255, 255, 255, 1 ),
                6px 6px 16px 0 rgba(217, 210, 200, 0.51)
            }
        }

    }   
    .home__lower__wrapper {
        width: 75%;
        margin: 1.25rem auto 0 auto;
        text-align: center;
        position: relative;
        z-index: 10;
        color: white;
        font-size: .8rem;
        display: grid;
        grid-template-columns: 1fr;
        .home__lower__grid {
            .register__link {
                color: #f39fdc;
                font-weight: 900;
            }
        }
    }
    .seller__lower {
        width: 75%;
        margin: 1.25rem auto 0 auto;
        text-align: center;
        position: relative;
        z-index: 10;
        color: white;
        font-size: .8rem;
        .seller__link {
            color: #f39fdc;
            font-weight: 900;
        }
    }
       

} 


